<template>
  <div class="dialogWrapper" v-show="visible">
    <div class="dialog">
      <header> {{title}}
        <span class="close" @click="close"> X </span>
      </header>
      <main>
        <slot></slot>
      </main>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'MyDialog',
    props: ['title', 'visible'], // 子组件需要用到的父组件中变化的参数
    methods: {
      // vue自定义事件
      close () {
        this.$emit('close') // 子组件向父组件通信：发送自定义事件名称
      }
    }
  }
</script>

<style scoped lang="less">
  .dialogWrapper {
    position: fixed;
    background: hsla(0, 0%, 0%, .25);
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    margin: 0;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .dialog {
    background: #fff;
    min-height: 4em;
    min-width: 10em;
    &>header {
      padding: 16px;
      display: flex;
      justify-content: space-between;
    }
    &>main {
      padding: 16px;
    }
  }
</style>



